
* {
    padding: 0;
    margin: 0;
}

img[data-v-5f7de381] {
    width: 100%;
}

.wh_content {
    position: relative;
    z-index: 1;
    overflow: hidden;
    width: 100%;
}
#wh_swiper {
    width: 100%;
    display: -webkit-box;
    display: -ms-flexbox;
    transition-duration: 0s linear;
}
.wh_indicator {
    position: absolute;
    bottom: 8px;
    width: 100%;
    text-align: center;
    background: 0 0;
}
.wh_indicator_item {
    display: inline-block;
    width: 8px;
    height: 8px;
    margin: 1px 7px;
    cursor: pointer;
    border-radius: 50%;
    background: #aaa;
}
.wh_show_bgcolor {
    background: #0fc37c;
}

.wh_slide {
    width: 100%;
    -ms-flex-negative: 0;
    flex-shrink: 0;
    z-index: 10;
    min-height: 100px;
}
